import React, { useEffect, useState } from 'react';
import database from './database/firebase';
import firebase from 'firebase';
function App() {
  const [task, setTask] = useState([]);
  const [input, setInput] = useState('');

  useEffect(()=>{
    database.collection('todo').orderBy('timestamps','desc').onSnapshot((snapshot)=>{
      setTask(snapshot.docs.map((doc)=>{ return doc.data().todo;}));
    })
  },[]);

  
 const handleTodo = (e)=>{
    e.preventDefault();
    // Add the task Data to the List
/*     setTask([...task, input]);
 */
    // Add data to the firebase collections
    database.collection('todo').add({
      "todo" : input,
      "timestamps" : firebase.firestore.FieldValue.serverTimestamp(),
      "status": ""  
    },{merge:true});
    setInput('');
  }

  return (
    <div className="App">
      <header className="App-header">
        {/* 
            Creating a todo app

            -- adding form consisting a text box and a button
            -- on button click the text should get added to the list 
            -- display all the todo list data (if any)
            -- edit and delete functionality for each list data 

            -- Designing with Material Ui 
            -- Integrate firebase


        */}
      </header>
      <div className="">
        <div className="todo__Form">
            <form>
              <input value ={input} onChange={e => setInput(e.target.value)}/>
              <button onClick={handleTodo}>Add Todo</button>
            </form>
        </div>
        <ul>
          {task.map((t)=>{
             return <li>{t}</li>
          })}
        </ul>
      </div>

    </div>
  );
}

export default App;
